<template>
  <div>
    <keep-alive>
      <router-view v-if='$route.meta.keepAlive'></router-view>
    </keep-alive>
    <router-view v-if='!$route.meta.keepAlive'></router-view>

  </div>
</template>

<script>
export default {
  name: 'laf-index',
  methods: {
    showLafList () {
      this.$router.push({ name: 'lafList' });
    },
    showLafDetail (itemID) {
      this.$router.push({
        name: 'lafDetail',
        params: {
          id: itemID,
        },
      })
    },
    showLafNew () {
      this.$router.push({
        name: 'lafNew'
      })
    },
  },
  mounted () {
    this.$bus.$on('showLafList', this.showLafList);
    this.$bus.$on('showLafDetail', this.showLafDetail);
    this.$bus.$on('showLafNew', this.showLafNew);
  },
  beforeDestroy () {
    this.$bus.$off('showLafList');
    this.$bus.$off('showLafDetail');
    this.$bus.$off('showLafNew');
  }
}
</script>

